ý trẻ\n                        </p></div></div></div></div></div></div></div></div></div> <section class=\"ss-add ss-feedbacks-full\" data-v-25d0b1f6><div class=\"add\" data-v-25d0b1f6><div class=\"add-list\" data-v-25d0b1f6><div class=\"add-full\" data-v-25d0b1f6><div class=\"add-slider\" data-v-25d0b1f6><div class=\"swiper loop\" data-v-25d0b1f6><div class=\"swiper-wrapper\" data-v-25d0b1f6><div class=\"swiper-slide\" data-v-25d0b1f6><div class=\"add-wrap\" data-v-25d0b1f6><div class=\"add-content\" data-v-25d0b1f6><img width=\"40\" height=\"40\" src=\"https://ischool.vn/template/assets/images/icon-quote.svg\" alt data-lazy-src=\"https://ischool.vn/template/assets/images/icon-quote.svg\" class=\"icon\" data-v-25d0b1f6> <div class=\"add-text\" data-v-25d0b1f6>\n                            Tôi đánh giá cao về sự nhiệt tình, năng động trong tác giảng dạy và năng lực của quý thầy cô tại trường. Bên cạnh việc\n                            học tập, nhà trường còn tổ chức nhiều sân chơi cho bé như: trồng rau để bé được tiếp cận với công viêc thường ngày, các\n                            lớp học năng khiếu: Tin, Tiếng Anh, Âm nhạc, Mỹ thuật đã giúp từng bé phát huy được năng lực, sở trường của mình, các\n                            hoạt động dã ngoại để bé được học hỏi trao dồi kiến thức và kỹ năng giao tiếp.\n                          </div> <div class=\"add-user\" data-v-25d0b1f6><div class=\"add-user-ava\" data-v-25d0b1f6><img width=\"227\" height=\"250\" src=\"https://ischool.vn/wp-content/uploads/2022/10/1-001-e1668409055428.jpg\" alt=\"Chị Huỳnh Thị Kim Hường\" data-lazy-srcset=\"https://ischool.vn/wp-content/uploads/2022/10/1-001-e1668409055428.jpg 227w, https://ischool.vn/wp-content/uploads/2022/10/1-001-e1668409055428-45x50.jpg 45w, https://ischool.vn/wp-content/uploads/2022/10/1-001-e1668409055428-163x180.jpg 163w\" data-lazy-sizes=\"(max-width: 227px) 100vw, 227px\" data-lazy-src=\"https://ischool.vn/wp-content/uploads/2022/10/1-001-e1668409055428.jpg\" class=\"img\" data-v-25d0b1f6></div> <div class=\"add-user-info\" data-v-25d0b1f6><p class=\"add-user-name\" data-v-25d0b1f6>Chị Huỳnh Thị Kim Hường</p></div></div></div></div></div> <div class=\"swiper-slide\" data-v-25d0b1f6><div class=\"add-wrap\" data-v-25d0b1f6><div class=\"add-content\" data-v-25d0b1f6><img width=\"40\" height=\"40\" src=\"https://ischool.vn/template/assets/images/icon-quote.svg\" alt data-lazy-src=\"https://ischool.vn/template/assets/images/icon-quote.svg\" class=\"icon\" data-v-25d0b1f6> <div class=\"add-text\" data-v-25d0b1f6>\n                            Phương rất thích cách học không áp lực, bé được tự do khám phá và được tìm hiểu những sở trường của mình. Dạo này bé còn\n                            thêm cả tiếng Anh khi trò chuyện nữa, nhiều từ mẹ chưa biết thì bé còn giải thích ngược lại cho mẹ… Mình thấy những điều\n                            mà hồi nhỏ mình không có được, không làm được thì giờ tại URI, mình mong con được hưởng trọn vẹn một nền giáo dục tốt\n                            tại Vinh hiện nay.\n                          </div> <div class=\"add-user\" data-v-25d0b1f6><div class=\"add-user-ava\" data-v-25d0b1f6><img width=\"600\" height=\"600\" src=\"https://ischool.vn/wp-content/uploads/2022/11/dien-vien-le-phuong.jpg\" alt=\"diễn viên lê phương\" data-lazy-srcset=\"https://ischool.vn/wp-content/uploads/2022/11/dien-vien-le-phuong.jpg 600w, https://ischool.vn/wp-content/uploads/2022/11/dien-vien-le-phuong-300x300.jpg 300w, https://ischool.vn/wp-content/uploads/2022/11/dien-vien-le-phuong-150x150.jpg 150w, https://ischool.vn/wp-content/uploads/2022/11/dien-vien-le-phuong-560x560.jpg 560w, https://ischool.vn/wp-content/uploads/2022/11/dien-vien-le-phuong-50x50.jpg 50w, https://ischool.vn/wp-content/uploads/2022/11/dien-vien-le-phuong-180x180.jpg 180w\" data-lazy-sizes=\"(max-width: 600px) 100vw, 600px\" data-lazy-src=\"https://ischool.vn/wp-content/uploads/2022/11/dien-vien-le-phuong.jpg\" class=\"img\" data-v-25d0b1f6></div> <div class=\"add-user-info\" data-v-25d0b1f6><p class=\"add-user-name\" data-v-25d0b1f6>Chị Lê Phương</p></div></div></div></div></div> <div class=\"swiper-slide\" data-v-25d0b1f6><div class=\"add-wrap\" data-v-25d0b1f6><div class=\"add-content\" data-v-25d0b1f6><img width=\"40\" height=\"40\" src=\"https://ischool.vn/template/assets/images/icon-quote.svg\" alt data-lazy-src=\"https://ischool.vn/template/assets/images/icon-quote.svg\" class=\"icon\" data-v-25d0b1f6><noscript data-v-25d0b1f6><img width=\"40\" height=\"40\" src=\"https://ischool.vn/template/assets/images/icon-quote.svg\" alt class=\"icon\" data-v-25d0b1f6></noscript> <div class=\"add-text\" data-v-25d0b1f6>\n                            Dưới sự dìu dắt và dạy dỗ của các thầy cô giáo, em dần dần tự tin hơn với bản thân mình. Chương trình học cân bằng giữa\n                            kiến thức ở lớp và các chương trình ngoại khóa, kỹ năng, âm nhạc, thể thao giúp em có nhiều hứng thú trong việc học tập.\n                            Bên cạnh đó, chương trình tiếng Anh với sự giao tiếp thường xuyên với giáo viên nước ngoài giúp em phát triển khả năng\n                            tiếng Anh, để có thể giao tiếp tự tin mọi lúc mọi nơi.\n                          </div> <div class=\"add-user\" data-v-25d0b1f6><div class=\"add-user-ava\" data-v-25d0b1f6><img width=\"380\" height=\"390\" src=\"https://ischool.vn/wp-content/uploads/2022/10/HO-VAN-CUONG-e1668409029913.jpg\" alt data-lazy-srcset=\"https://ischool.vn/wp-content/uploads/2022/10/HO-VAN-CUONG-e1668409029913.jpg 380w, https://ischool.vn/wp-content/uploads/2022/10/HO-VAN-CUONG-e1668409029913-292x300.jpg 292w, https://ischool.vn/wp-content/uploads/2022/10/HO-VAN-CUONG-e1668409029913-50x50.jpg 50w, https://ischool.vn/wp-content/uploads/2022/10/HO-VAN-CUONG-e1668409029913-175x180.jpg 175w\" data-lazy-sizes=\"(max-width: 380px) 100vw, 380px\" data-lazy-src=\"https://ischool.vn/wp-content/uploads/2022/10/HO-VAN-CUONG-e1668409029913.jpg\" class=\"img\" data-v-25d0b1f6></div> <div class=\"add-user-info\" data-v-25d0b1f6><p class=\"add-user-name\" data-v-25d0b1f6>Em Hồ Văn Cường</p></div></div></div></div></div> <div class=\"swiper-slide\" data-v-25d0b1f6><div class=\"add-wrap\" data-v-25d0b1f6><div class=\"add-content\" data-v-25d0b1f6><img width=\"40\" height=\"40\" src=\"https://ischool.vn/template/assets/images/icon-quote.svg\" alt data-lazy-src=\"https://ischool.vn/template/assets/images/icon-quote.svg\" class=\"icon\" data-v-25d0b1f6><noscript data-v-25d0b1f6><img width=\"40\" height=\"40\" src=\"https://ischool.vn/template/assets/images/icon-quote.svg\" alt class=\"icon\" data-v-25d0b1f6></noscript> <div class=\"add-text\" data-v-25d0b1f6>\n                            Tôi cũng là một người mẹ như bao người khác, đều mong muốn các con lớn lên có một tương lai rộng mở, nên tôi chọn trường\n                            cho con cũng rất thận trọng. Bởi vì, tôi biết một ngôi trường tốt có ảnh hưởng rất lớn đến tính cách, học vấn và tương\n                            lai sau này của con. Tại URI, tôi thấy môi trường học tập không gò bó, giúp các con tự tư duy phát triển trí tuệ, các\n                            thầy cô tại trường cũng rất thân thiện và gần gũi. Tôi chân thành cảm ơn các thầy cô tại trường đã luôn tạo điều kiện để\n                            các em phát huy tối đa khả năng của bản thân.\n                          </div> <div class=\"add-user\" data-v-25d0b1f6><div class=\"add-user-ava\" data-v-25d0b1f6><img width=\"345\" height=\"365\" src=\"https://ischool.vn/wp-content/uploads/2022/10/chi-La-Thuy-Tran-phu-huynh-iSchool-e1668409852863.jpg\" alt data-lazy-srcset=\"https://ischool.vn/wp-content/uploads/2022/10/chi-La-Thuy-Tran-phu-huynh-iSchool-e1668409852863.jpg 345w, https://ischool.vn/wp-content/uploads/2022/10/chi-La-Thuy-Tran-phu-huynh-iSchool-e1668409852863-284x300.jpg 284w, https://ischool.vn/wp-content/uploads/2022/10/chi-La-Thuy-Tran-phu-huynh-iSchool-e1668409852863-47x50.jpg 47w, https://ischool.vn/wp-c